var canvas = document.getElementById('output'),
    ctx = canvas.getContext("2d"),
    ParticleSystem = function(opts) {
        var defaultOpts = {
            time: Date.now(),
            emissionRate: 60,
            speed: {
                x: 0,
                y: 5
            },
            position: {
                x: canvas.width / 2,
                y: canvas.height * 0.75
            },
            particles: [],
            generateParticle: function() {
                obj.particles.push(new Particle(obj, Date.now()));
            },
            colourGradient: new ColorAnimation(),
            sizeGradient: new Animation(),
            animateParticles: function() {
                var deltaTime = Date.now() - this.time,
                    i = 0,
                    p = this.particles,
                    l = p ? p.length : 0,
                    position = this.position;
                for (i = 0; i < l; ++i) {
                    p[i].render(position, deltaTime);
                }
            },
            update: function() {
                this.animateParticles();
            },
            init: function() {
                Controller.addElement(this);
                setInterval(obj.generateParticle, 1000 / this.emissionRate);
            }
        }
        var obj = Object.assign({}, defaultOpts, opts);
        obj.init();
        return obj;
    },
    Particle = function(parent, startTime, opts) {
        var defaultOpts = {
            speed: {
                x: 0,
                y: 250
            },
            size: 50,
            lifeTime: 1,
            getSize: function() {
                return this.size * parent.sizeGradient.getValueAtTime(this.deltaTime / this.lifeTime, true);
            },
            position: {
                x: 0,
                y: 0
            },
            time: 0,
            deltaTime: 0,
            render: function() {
                this.deltaTime = (Date.now() - this.time) / 1000;
                if (this.deltaTime > this.lifeTime)
                    return;
                var size = this.getSize();
                ctx.beginPath();
                ctx.arc(this.origin.x + this.position.x, this.origin.y + this.position.y - this.speed.y * this.deltaTime, size, 0, Math.PI * 2);
                ctx.fillStyle = parent.colourGradient.getValueAtTime(this.deltaTime / this.lifeTime, true);
                ctx.fill();
            },
            randomize: function() {
                this.lifeTime *= Math.random() * 0.4 + 0.8;
                this.position.x += Math.random() * 40 - 20;
                this.position.y += Math.random() * 10 - 5;
                this.speed.y += Math.random() * 10 - 5;
            }
        }

        var obj = Object.assign({}, defaultOpts, opts);
        obj.time = startTime;
        obj.parent = parent;
        obj.origin = {
            x: parent.position.x,
            y: parent.position.y
        };
        obj.randomize();
        return obj;
    },
    Controller = {
        elements: [],
        update: function() {
            Controller.clearCanvas();
            for (var i = 0, l = Controller.elements.length; i < l; ++i) {
                Controller.elements[i].update();
            }
            requestAnimationFrame(Controller.update);
        },
        clearCanvas: function() {
            canvas.width = canvas.width;
        },
        addElement: function(el) {
            Controller.elements.push(el);
        }

    },
    Animation = function() {
        return {
            frames: {0:0,1:1},
            addFrame: function(time, value) {
                this.frames[time] = value;
            },
            getValueAtTime: function(time, interpolate) {
                var prevFrame, nextFrame, prevFrameTime, delta, keys = [];
                for (var k in this.frames) {

                    keys.push(parseFloat(k));
                }
                keys.sort();
                for (var i = 0, l = keys.length; i < l; ++i) {
                    k = keys[i]
                    if (time < k) {
                        nextFrame = this.frames[k];
                        break;
                    }
                    prevFrameTime = k;
                    prevFrame = this.frames[k];
                }

                if (interpolate) {
                    return prevFrame + (nextFrame - prevFrame) * ((time - prevFrameTime) * 1 / (k - prevFrameTime));
                }
                return prevFrame;
            }
        }
    },
    ColorAnimation = function() {
        return {
            r: new Animation(),
            g: new Animation(),
            b: new Animation(),
            a: new Animation(),
            addFrame: function(time, rgb) {
                if (typeof rgb == "string")
                    rgb = hexToRgb(rgb);
                this.r.addFrame(time, rgb.r);
                this.g.addFrame(time, rgb.g);
                this.b.addFrame(time, rgb.b);
                this.a.addFrame(time, rgb.a);
            },
            getValueAtTime: function(time, interpolate) {
                var r = Math.round(this.r.getValueAtTime(time, interpolate)).toString(16),
                    g = Math.round(this.g.getValueAtTime(time, interpolate)).toString(16),
                    b = Math.round(this.b.getValueAtTime(time, interpolate)).toString(16),
                    a = this.a.getValueAtTime(time, interpolate);
                ctx.globalAlpha = a;
                return '#' + (r.length > 1 ? r : '0' + r) + (g.length > 1 ? g : '0' + g) + (b.length > 1 ? b : '0' + b);
            }

        }
    };

requestAnimationFrame(Controller.update);

window.addEventListener('resize', calculateCanvasWidth);

function calculateCanvasWidth() {
    canvas.width = window.innerWidth;
    canvas.height = window.innerHeight;
}
calculateCanvasWidth();

var ps = new ParticleSystem();

ps.colourGradient.addFrame(0, '#d5000000');
ps.colourGradient.addFrame(0.25, '#ff980088');
ps.colourGradient.addFrame(0.66, '#44444433');
ps.colourGradient.addFrame(1, '#22222200');
ps.sizeGradient.addFrame(0, 0.25);
ps.sizeGradient.addFrame(0.25, 1);
ps.sizeGradient.addFrame(0.66, 0.2);
ps.sizeGradient.addFrame(1, 0.75);
ps.generateParticle();

canvas.addEventListener('mousemove', function(e) {
    ps.position.x = e.clientX;
    ps.position.y = e.clientY;
})

function hexToRgb(hex) {
    var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})?$/i.exec(hex);
    return result ? {
        r: parseInt(result[1], 16),
        g: parseInt(result[2], 16),
        b: parseInt(result[3], 16),
        a: typeof result[4] == 'undefined' ? 1 : parseInt(result[4], 16) / 255
    } : null;
}